.detail {
  &__photos {
    @include pageGrid;

    margin-bottom: 16px;
  }

  &__link {
    position: relative;

    &--wide {
      @include onTablet {
        grid-column: span 4;
      }

      @include onDesktop {
        grid-column: span 8;
      }
    }

    &--square {
      height: 0;
      padding-bottom: 100%;

      @include onTablet {
        grid-column: span 2;
      }

      @include onDesktop {
        grid-column: span 4;
      }
    }
  }

  &__photo {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform 500ms;

    @include hover(transform, scale(1.1));

  }

  &__title {
    font-weight: 700;
    font-size: 24px;
    line-height: 33.6px;
    margin-bottom: 8px;
  }

  &__description {
    margin-bottom: 48px;
  }
}
